.contenedor-viaje {
  width: 1170px;
  height: 368px;
  margin: 10px 10px 50px;
  display: flex;
  align-items: stretch;
  background-color: whitesmoke;
  box-shadow: 0 3px 13px 1px rgb(0 0 0 / 9%);
}

.imagen-viaje {
  height: 100%;
  width: auto;
  flex-shrink: 0;
}

.contenedor-texto-viaje {
  flex: 1;
  justify-content: center;
  text-align: center;
  padding: 40px;
  font-family: 'Century Gothic', sans-serif; 
  font-weight: 400;
  line-height: 1.5rem;
}

.nombre-viaje {
  font-size: 1.3rem;
}

.fecha-viaje {
  font-size: 1.3rem;
  margin-top: 15px;
  padding-bottom: 30px;
}

.texto-viaje {
  font-size: 1.1rem;
  text-align: justify;
}

/* Media Query para pantallas pequeñas */
@media (max-width: 768px) {
  .contenedor-viaje {
    flex-direction: column;
    max-width: 90%; /* Reduce el ancho en pantallas pequeñas */
  }

  .imagen-viaje,
  .contenedor-texto-viaje {
    width: 100%;
  }

  .contenedor-texto-viaje {
    padding: 15px;
  }
}