.contador {
	min-width: 300px;
	height: 300px;
	font-size: 160px;
	padding: 25px;
	color: white;
	display: flex;
	align-items: center;
	justify-content: center;
	border: 8px solid white;
	margin-top: 50px;
	margin-bottom: 50px;
	border-radius: 30px;
	box-shadow: 0 0 20px rgba(255, 255, 255, 0.6); 
	transition: all 0.3s ease-in-out; 
}

.contador:hover {
	transform: scale(1.05);
	border-color: white;
}