.menu-desplegable {
  position: fixed;
  top: 0;
  left: 0;
  width: 200px;
  height: 100px;
  padding: 10px;
  transform: translateX(-100%); /* Empieza fuera de la pantalla, a la izquierda */
  transition: transform 0.5s ease-in-out; /* Transición suave */
}

.menu-desplegable.menu-visible {
  transform: translateX(0); /* Cuando es visible, vuelve a su posición original */
}

.menu-listado {
  list-style-type: none;
  padding: 0;
  margin: 0;
}